*{
  box-sizing: border-box;
}
body{
  padding: 0;
  margin: 0;
}

.container{
  display: grid;
  grid-template-columns: 2fr 4fr;
  height: 100vh;
  
}
.side-view{
  padding: 1px 40px;
  margin: 0;
  background-color: rgb(4, 4, 49);
  color: white;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 100%;
  line-height: 1.5rem;
}
.side-view a{
  text-decoration: none;
  color: white;
}
.side-view a:hover{
  border-bottom: 1px solid white;
  color: blue;
}
i{
  color: rgb(177, 173, 173);
}
.main-view{
  padding: 5px 50px;
  width: 60%;
  line-height: 2rem;
}
hr{
  width: 10%;
  margin-left: 0;
}
h1{
  margin:  0;
  padding: 0;
}
h3{
  margin: 0;
  margin-top: 20px;
  padding: 0;

}
.intro p{
  margin: 0;
  margin-top: 3px;
  padding: 0;
}

@media (max-width: 1280px){
  .main-view{width: 100%;}
}
@media (max-width: 700px){
.container{
  display: flex;
  flex-direction: column-reverse;
  height: auto;
  }
.main-view, .side-view{padding: 20px 50px;}
}

